import {
	mapState
} from 'vuex'

export default {
	computed: mapState([
		'hasLogin', 
	]),
	onLoad() {
		// var status = uni.getStorageSync('status')
		// console.log("Login==", this.hasLogin);
		var status = uni.getStorageSync('status')
		if(status == 1) {
			uni.switchTab({
				url:'../index/index'
			})
		} else {
			uni.navigateTo({
				url: '../login/login'
			});
		}
	}
}
